<script>
// import { mapState } from "vuex";
export default {
	name: "regiSter",
	data() {
		return {
			//--
			phone: "",
			//
			code: "",
			//
			password: "",
			//
			password1: "",
			//
			agree: true,
		};
	},
	methods: {
		//
		async getCode() {
			try {
				//
				const { phone } = this;
				phone && (await this.$store.dispatch("getCode", phone));
				//codecode
				this.code = this.$store.state.user.code;
			} catch (error) {
				alert(error.message);
			}
		},
		//
		userRegister() {
			try {
				const { phone, code, password, password1 } = this;
				phone &&
					code &&
					password &&
					password1 &&
					this.$store.dispatch("userRegister", {
						phone,
						code,
						password,
						password1,
					});
				//
				this.$router.push("/login");
			} catch (error) {
				alert(error.message);
			}
		},
	},
	computed: {},
};
</script>

