<template>
	<!--  -->
	<div class="type-nav">
		<div class="container">
			<!-- |div,h2sort -->
			<div @mouseleave="leaveShow" @mouseenter="enterShow">
				<h2 class="all"></h2>
				<!--  -->
				<transition name="sort">
					<div class="sort" v-show="show">
						<div class="all-sort-list2" @click="goSearch">
							<!-- categoryList.slice(0,16) -->
							<div
								class="item"
								v-for="(c1,index) in categoryList"
								:key="c1.categoryId"
								:class="{cur:currentIndex==index}"
							>
								<h3 @mouseenter="changeIndex(index)">
									<a
										:data-categoryName="c1.categoryName"
										:data-category1Id="c1.categoryId"
									>{{c1.categoryName}}</a>
								</h3>
								<!-- , -->
								<div class="item-list clearfix" :style="{display:currentIndex==index?'block':'none'}">
									<div class="subitem" v-for="(c2) in c1.categoryChild" :key="c2.categoryId">
										<dl class="fore">
											<dt>
												<a
													:data-categoryName="c2.categoryName"
													:data-category2Id="c2.categoryId"
												>{{c2.categoryName}}</a>
											</dt>
											<dd>
												<em v-for="(c3) in c2.categoryChild" :key="c3.categoryId">
													<a
														:data-categoryName="c3.categoryName"
														:data-category3Id="c3.categoryId"
													>{{c3.categoryName}}</a>
												</em>
											</dd>
										</dl>
									</div>
								</div>
							</div>
						</div>
					</div>
				</transition>
			</div>
			<nav class="nav">
				<a href="###"></a>
				<a href="###"></a>
				<a href="###"></a>
				<a href="###"></a>
				<a href="###"></a>
				<a href="###"></a>
				<a href="###"></a>
				<a href="###"></a>
			</nav>
		</div>
	</div>
</template>
<script>
import { mapState } from "vuex";
//throttle
import throttle from "lodash/throttle";

export default {
	name: "typeNav",
	components: {},
	mixins: [],
	props: {},
	data() {
		return {
			//
			currentIndex: -1,
			show: true,
		};
	},
	computed: {
		...mapState({
			//
			//state
			categoryList: (state) => {
				return state.home.categoryList;
			},
		}),
	},
	watch: {},
	mounted() {
		// console.log(this);
		// console.log(this.$router);
		// console.log(this.$route);

		//showfalse
		//hometypeNav
		if (this.$route.path != "/home") {
			this.show = false;
		}
	},
	methods: {
		// changeIndex(index) {
		// 	//index:
		// 	this.currentIndex = index;
		// },
		changeIndex: throttle(function (index) {
			this.currentIndex = index;
		}, 50),
		/*ES5changeIndexkeythrottle()value
		index(@mouseenter="changeIndex(index)) throttle*/

		// leaveIndex() {
		// 	this.currentIndex = -1;
		// },
		goSearch(e) {
			/*
			2.h5()
			4.searchahref
			5.url4ahref
			*/

			//@@@@@@@@@@@  queryroute
			//

			let element = e.target;
			// console.log(element.dataset);
			//dataset
			let { categoryname, category1id, category2id, category3id } =
				element.dataset;
			if (categoryname) {
				//
				let location = { name: "search" };
				let query = { categoryName: categoryname };
				//a
				if (category1id) {
					query.category1id = category1id;
				} else if (category2id) {
					query.category2id = category2id;
				} else {
					query.category3id = category3id;
				}
				if (this.$route.params) {
					//
					location.query = query;
					location.params = this.$route.params;
					//
					this.$router.push(location);
				}
			}
		},
		//
		enterShow() {
			this.show = true;
		},
		//
		leaveShow() {
			this.currentIndex = -1;
			if (this.$route.path != "/home") {
				this.show = false;
			}
		},
	},
};
</script>
<style lang='less' scoped>
.type-nav {
	border-bottom: 2px solid #e1251b;
	.container {
		width: 1200px;
		margin: 0 auto;
		display: flex;
		position: relative;

		.all {
			width: 210px;
			height: 45px;
			background-color: #e1251b;
			line-height: 45px;
			text-align: center;
			color: #fff;
			font-size: 14px;
			font-weight: bold;
		}

		.nav {
			a {
				height: 45px;
				margin: 0 22px;
				line-height: 45px;
				font-size: 16px;
				color: #333;
			}
		}

		.sort {
			position: absolute;
			left: 0;
			top: 45px;
			width: 210px;
			height: 461px;
			position: absolute;
			background: #fafafa;
			z-index: 999;

			.all-sort-list2 {
				.item {
					h3 {
						line-height: 27px;
						font-size: 14px;
						font-weight: 400;
						overflow: hidden;
						padding: 0 20px;
						margin: 0;

						a {
							text-decoration: none;
							color: #333;
						}
					}
					// :hover {
					// 	background-color: rgb(230, 186, 183);
					// }
					.item-list {
						display: none;
						position: absolute;
						width: 734px;
						min-height: 460px;
						background: #f7f7f7;
						left: 210px;
						border: 1px solid #ddd;
						top: 0;
						z-index: 9999 !important;

						.subitem {
							float: left;
							width: 650px;
							padding: 0 4px 0 8px;

							dl {
								border-top: 1px solid #eee;
								padding: 6px 0;
								overflow: hidden;
								zoom: 1;

								&.fore {
									border-top: 0;
								}

								dt {
									float: left;
									width: 54px;
									line-height: 22px;
									text-align: right;
									padding: 3px 6px 0 0;
									font-weight: 700;
								}

								dd {
									float: left;
									width: 415px;
									padding: 3px 0 0;
									overflow: hidden;

									em {
										float: left;
										height: 14px;
										line-height: 14px;
										padding: 0 8px;
										margin-top: 5px;
										border-left: 1px solid #ccc;
									}
								}
							}
						}
					}
				}
				.cur {
					background-color: rgb(250, 220, 225);
				}
				// .item:hover {
				// 	background-color: thistle;
				// }
			}
		}
		//
		.sort-enter {
			opacity: 0;
		}
		//
		.sort-enter-to {
			opacity: 1;
		}
		//
		.sort-enter-active {
			transition: all 0.4s linear;
			//
			overflow: hidden;
		}
	}
}
</style>