<script>
import { mapGetters } from "vuex";
import ImageList from "./ImageList/ImageList.vue";
import Zoom from "./Zoom/Zoom.vue";

export default {
	name: "detailVue",
	data() {
		return {
			skuNum: 1,
		};
	},
	components: {
		ImageList,
		Zoom,
	},
	methods: {
		//
		changeActive(a, b) {
			// console.log(a, b);
			b.forEach((item) => {
				item.isChecked = 0;
			});
			//
			a.isChecked = 1;
		},
		//
		changeSkuNum(event) {
			// * 1
			let value = event.target.value * 1;
			//NaN1
			if (isNaN(value) || value < 1) {
				this.skuNum = 1;
			} else {
				//1 
				this.skuNum = parseInt(value);
			}
		},
		//
		async addShopCar() {
			//1.

			//2.
			//3.
			// addOrUpdateShopCartasyncPromise
			try {
				await this.$store.dispatch("addOrUpdateShopCart", {
					skuId: this.$route.params.skuid,
					skuNum: this.skuNum,
				});
				//
				sessionStorage.setItem("SKUINFO", JSON.stringify(this.skuInfo));
				this.$router.push({
					name: "addcartsuccess",
					query: { skuNum: this.skuNum },
				});
			} catch (error) {
				alert(error.message);
			}
		},
	},
	mounted() {
		//action
		this.$store.dispatch("getGoodInfo", this.$route.params.skuid);
	},
	computed: {
		...mapGetters([
			"categoryView",
			"skuInfo",
			"skuAttrValueList",
			"spuSaleAttrList",
		]),
		//
		skuImageList() {
			//skuInfo
			return this.skuInfo.skuImageList || [];
		},
	},
};
</script>

