<template>
	<div class="cart">
		<h4></h4>
		<div class="cart-main">
			<div class="cart-th">
				<div class="cart-th1"></div>
				<div class="cart-th2"></div>
				<div class="cart-th3"></div>
				<div class="cart-th4"></div>
				<div class="cart-th5"></div>
				<div class="cart-th6"></div>
			</div>
			<div class="cart-body">
				<ul class="cart-list" v-for="(cart) in cartInfoList" :key="cart.id">
					<li class="cart-list-con1">
						<input
							type="checkbox"
							name="chk_list"
							:checked="cart.isChecked==1"
							@change="updateChecked(cart,$event)"
						/>
					</li>
					<li class="cart-list-con2">
						<img :src="cart.imgUrl" />
						<div class="item-msg">{{cart.skuName}}</div>
					</li>
					<li class="cart-list-con4">
						<span class="price">{{cart.skuPrice}}.00</span>
					</li>
					<li class="cart-list-con5">
						<a href="javascript:void(0)" class="mins" @click="handler('min',-1,cart)">-</a>
						<input
							autocomplete="off"
							type="text"
							minnum="1"
							class="itxt"
							:value="cart.skuNum"
							@change="handler('change',$event.target.value*1,cart)"
						/>
						<a href="javascript:void(0)" class="plus" @click="handler('add',1,cart)">+</a>
					</li>
					<li class="cart-list-con6">
						<span class="sum">{{cart.skuNum * cart.skuPrice}}</span>
					</li>
					<li class="cart-list-con7">
						<a href="#none" class="sindelet" @click="deleteCartById(cart)"></a>
						<br />
						<a href="#none"></a>
					</li>
				</ul>
			</div>
		</div>
		<div class="cart-tool">
			<div class="select-all">
				<input
					class="chooseAll"
					type="checkbox"
					:checked="isAllchecked&&cartInfoList.length>0"
					@change="updateAllCartChecked"
				/>
				<span></span>
			</div>
			<div class="option">
				<a @click="deleteAllcheckedcart"></a>
				<a href="#none"></a>
				<a href="#none"></a>
			</div>
			<div class="money-box">
				<div class="chosed">
					
					<span>0</span>
				</div>
				<div class="sumprice">
					<em> </em>
					<i class="summoney">{{totalPrice}}.00</i>
				</div>
				<div class="sumbtn">
					<router-link class="sum-btn" to="/trade"></router-link>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
// import throttle from "lodash/throttle";
import debounce from "lodash/debounce";
import { mapGetters } from "vuex";

export default {
	name: "shopCart",
	mounted() {
		this.getData();
	},
	methods: {
		getData() {
			this.$store.dispatch("getCartList");
		},
		//
		/**
		dataskunum
		index
		 
		 cart.skuNum>1-1
		 cart.skuNum<0,
		 500ms 
		 */
		handler: debounce(async function (type, disNum, cart) {
			//this

			//type:
			//disNum:
			//cart
			switch (type) {
				case "add":
					disNum = 1;
					break;
				case "min":
					if (cart.skuNum <= 1) return;
					disNum = cart.skuNum > 1 ? -1 : 0;
					break;
				case "change":
					//
					if (isNaN(disNum) || disNum < 1) {
						disNum = 0;
					} else {
						//-
						disNum = parseInt(disNum) - cart.skuNum;
					}
					break;
			}
			//action
			try {
				await this.$store.dispatch("addOrUpdateShopCart", {
					skuId: cart.skuId,
					//skuNum
					skuNum: disNum,
				});

				//
				this.getData();
			} catch (error) {
				console.log(error.message);
			}
		}, 600),
		async deleteCartById(cart) {
			try {
				await this.$store.dispatch("deleteCartList", cart.skuId);
				//
				this.getData();
			} catch (error) {
				alert(error.message);
			}
		},
		//
		async updateChecked(cart, event) {
			//isChecked0|1
			try {
				//
				let isChecked = event.target.checked ? "1" : "0";
				await this.$store.dispatch("UpdateCartList", {
					skuId: cart.skuId,
					isChecked,
				});
				this.getData();
			} catch (error) {
				alert(error.message);
			}
		},
		//
		async deleteAllcheckedcart() {
			try {
				await this.$store.dispatch("deleteAllcheckedcart");
				//action
				this.getData();
			} catch (error) {
				alert(error.message);
			}
		},
		//
		async updateAllCartChecked(event) {
			try {
				let isChecked = event.target.checked ? "1" : "0";
				//action
				await this.$store.dispatch("updateAllCartChecked", isChecked);
				this.getData();
			} catch (error) {
				alert(error.message);
			}
		},
	},
	computed: {
		...mapGetters(["cartList"]),
		//
		cartInfoList() {
			return this.cartList.cartInfoList || [];
		},
		//
		totalPrice() {
			let sum = 0;
			this.cartInfoList.forEach((element) => {
				sum += element.skuNum * element.skuPrice;
			});
			return sum;
		},
		//
		isAllchecked() {
			//1
			return this.cartInfoList.every((item) => item.isChecked == 1);
		},
	},
};
</script>

